body {
  background-color: #14131f;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active,
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.header {
  width: 100%;
  background-color: #7fdbff;
  padding: 10px 20px;
}

.vkButton {
  display: inline-block;
  padding: 8px;
  color: #0a0e33;
  font-size: 14px;
  border-left: 1px solid #0a0e33;
}

.vkButton:last-child {
  border-right: 1px solid #0a0e33;
}

.link {
  color: inherit;
  text-decoration: none;
}

.page {
  display: block;
}

.text {
  margin-top: 50px;
  margin-left: 50px;
  color: #7fdbff;
  font-size: 20px;
}